body {
  margin: 0;
}

.mainColor {
  text-align: center;
  width: 25vw;
  height: 25vh;
  display: inline-block;
}

.lighterColor {
  text-align: center;
  width: 50%;
  height: 50%;
  display: inline-block;
}

.darkerColor {
  width: 50%;
  height: 50%;
  display: inline-block;
}

@media (orientation: portrait) {
  .mainBackground {
    flex-direction: column;
    justify-content: space-between;
    width: 100dvw;
    height: 100dvh;
    display: flex;
  }
}

@media (orientation: landscape) {
  .mainBackground {
    flex-direction: column;
    justify-content: space-between;
    width: 100dvw;
    height: 100dvh;
    display: flex;
  }
}

@media (width <= 600px) {
  .page-header {
    flex-shrink: 2;
    max-width: 100dvw;
    height: auto;
  }
}

@media (width >= 600px) {
  .page-header {
    max-width: 100dvw;
    height: auto;
  }
}

@media (width <= 600px) {
  .page-footer {
    justify-content: end;
    justify-self: end;
    align-items: end;
    min-height: 10dvh;
    max-height: 20dvh;
  }
}

@media (width >= 600px) {
  .page-footer {
    flex-shrink: 2;
    justify-content: end;
    justify-self: end;
    align-items: end;
    min-height: 10dvh;
    max-height: 20dvh;
  }
}

.page-title {
  width: 100%;
  height: auto;
  font-size: 3rem;
}

@media (width >= 600px) {
  .login-form-container {
    flex-grow: 2;
    justify-content: space-around;
    align-self: center;
    align-items: center;
    width: 85%;
    height: 85%;
    display: flex;
  }
}

@media (width <= 600px) {
  .login-form-container {
    flex-direction: column;
    flex-grow: 2;
    justify-content: center;
    align-items: center;
    max-width: 100%;
    height: auto;
    display: flex;
  }

  .login-form {
    flex-direction: column;
    place-content: center;
    align-items: center;
    max-width: 100%;
    height: auto;
    display: flex;
  }
}

@media (width >= 600px) {
  .login-form {
    flex-direction: column;
    place-content: center;
    align-items: center;
    max-width: 100%;
    height: 100%;
    display: flex;
  }
}

.loading-indicator {
  opacity: .7;
  place-self: center;
}
